import React, { useRef } from "react";
import { Helmet } from "react-helmet";

import UnderstandingInklua from "../../components/UnderstandingInklua";
import ModuleOne from "../../components/ModuleOne";
import RetrieveIncoins from "../../components/RetrieveIncoins";
import HowItWorksBanner from "../../components/HowItWorksBanner";
import SquareLinkButton from "../../components/SquareLinkButton";

import FirstModuleImage from "./assets/ilustracao-modulo-1.png";
import FirstModuleImage2x from "./assets/ilustracao-modulo-1@2x.png";
import FirstModuleImage3x from "./assets/ilustracao-modulo-1@3x.png";
import HappyGroupImage from "./assets/grupo-feliz.png";
import HappyGroupImage2x from "./assets/grupo-feliz@2x.png";
import HappyGroupImage3x from "./assets/grupo-feliz@3x.png";
import SecondModuleImage from "./assets/ilustracao-modulo-2.png";
import SecondModuleImage2x from "./assets/ilustracao-modulo-2@2x.png";
import SecondModuleImage3x from "./assets/ilustracao-modulo-2@3x.png";

const HowItWorks = () => {
  const firstBlockRef = useRef();

  const handleScroll = () => {
    const offset = firstBlockRef.current.offsetTop;

    window.scroll({
      top: offset - 80,
      left: 0,
      behavior: "smooth",
    });
  };

  return (
    <>
      <Helmet>
        <title>Inklua</title>
        <link rel="canonical" href={window.location.href} />
        <meta
          property="og:description"
          content="A Inklua atua com incluso de pessoas com deficincia no mercado de trabalho atravs dos servios de recrutamento e seleo, sensibilizao e mapeamento"
        ></meta>
        <meta
          property="description"
          content="A Inklua atua com incluso de pessoas com deficincia no mercado de trabalho atravs dos servios de recrutamento e seleo, sensibilizao e mapeamento"
        ></meta>
      </Helmet>
      <HowItWorksBanner handleAction={handleScroll} />
      <UnderstandingInklua ref={firstBlockRef} />
      <ModuleOne
        title="Mais do que ouro"
        subtitle="As valiosas <b>Inkoins</b>"
        description="
          <p>Elas so nossas moedas de troca. Aps se cadastrar na Inklua, voc poder obt-las clicando em nossos anncios ou compartilhando nossas vagas.</p>
          <p>Cada Inkoin resgatada representa um valor em dinheiro real. <strong>Por exemplo: ao resgatar Inkoins de um anncio, o valor arrecadado pela Inklua com seu clique estar presente nas Inkoins que acabou de resgatar.</strong></p>
        "
        image={[FirstModuleImage, FirstModuleImage2x, FirstModuleImage3x]}
      />
      <ModuleOne
        title="Abradacabra"
        subtitle="A mgica da <br /><b>colaborao</b>"
        description="<p>Aps resgatar Inkoins voc poder do-las para uma causa e  justamente nesse momento em que a mgica acontece: <strong>suas Inkoins se transformam em dinheiro de verdade e so depositadas na caixinha da causa.</strong></p>"
        image={[SecondModuleImage, SecondModuleImage2x, SecondModuleImage3x]}
        inverse
        action={
          <SquareLinkButton
            text="Conhea a causa da vez"
            url="/area-de-doacoes"
          />
        }
      />
      <ModuleOne
        title="Cada ao gera uma reao"
        subtitle="O resultado do <b>sucesso</b>"
        description="<p>Quando tivermos o valor necessrio para realizar a causa, faremos uma ou mais postagens dentro da plataforma mostrando o <b>impacto que suas Inkoins proporcionaram na vida das pessoas que voc decidiu ajudar.</b></p>"
        image={[HappyGroupImage, HappyGroupImage2x, HappyGroupImage3x]}
      />
      <RetrieveIncoins />
    </>
  );
};

export default HowItWorks;
