import React from "react";

import styles from "./styles.module.scss";

const PrivacyPolicyContent = () => (
  <main className={styles.wrapper}>
    <small className={styles.preffix}>
      A poltica de privacidade inicia vigncia em 2 de abril de 2021
    </small>
    <h1>Poltica de privacidade e reteno de dados pessoais</h1>
    <p>
      Os dados no so acessados por terceiros e no comercializamos dados de
      nenhum usurio.
    </p>
    <p>A no aceitao deste termo impossibilita a utilizao da plataforma.</p>
    <p>
      Para cancelar o aceite em relao a utilizao de seus dados, o usurio
      dever excluir sua conta da plataforma.
    </p>
    <h2>Utilizao de dados pessoais:</h2>
    <p>
      Nome e endereo de e-mail so coletados caso o login seja feito com alguma
      rede social. A utilizao destes dados  somente para comunicao, ao
      enviar e-mails e personalizar seu usurio.
    </p>
    <h2>Dados de navegao e dos dispositivos:</h2>
    <p>
      Para realizar anlises, personalizar e desenvolver novas melhorias e
      funcionalidades realizamos o armazenamento dos dados de sua navegao em
      nossa plataforma, bem como os identificadores nicos de e modelos dos seus
      dispositivos.
    </p>
    <h2>Direito de retificao:</h2>
    <p>
      Ao acessar seu perfil o usurio pode editar os dados, se funo para a
      edio no estiver disponvel no meio acessado a alterao de dados pode
      ser solicitada mediante contato no chat, com prazo de concluso em at 10
      dias teis.
    </p>
    <h2>Direito ao esquecimento:</h2>
    <p>
      O usurio pode excluir sua conta ao acessar seu perfil, se a funo para a
      excluso do usurio no estiver disponvel no meio acessado a excluso da
      conta pode ser solicitada mediante contato pelo e-mail
      contato@inklua.com.br, com prazo de concluso em at 10 dias teis. Ao
      excluir a conta os dados pessoais so anonimizados em nosso banco de
      dados, os registros referentes a doaes sero preservados, porm no 
      possvel identificar o usurio que realizou as doaes e tampouco
      recuperar esses registros.
    </p>
    <h2>Direito a portabilidade:</h2>
    <p>
      Para receber seus dados pessoais e registros de doaes efetuadas deve-se
      solicitar o envio dos mesmos pelo e-mail contato@inklua.com.br, com prazo
      de concluso em at 10 dias teis. O envio ser feito em arquivo no
      formato JSON.
    </p>
  </main>
);

export default PrivacyPolicyContent;
